.App{
  background-color: #ffffff;
  height: 100vh;
  color: #515151;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.fieldText{
  width: 50%;
  padding: 30px;
}

.fieldText h2{
  color: crimson;
}

h3{
  color: crimson;
  padding-left: 18px;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  min-width: 420px;
  height: 50%;
  width: 35%;
  border-radius: 50px;
  background: #ffffff;
  box-shadow:   22px 22px 65px #c4c4c4,
               -22px -22px 65px #ffffff;
}

.field{
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
  width: 90%;
}

.input{
  font-size: 16px;
  display: block;
  border: none;
  border-bottom: 1px solid #515151;
  background: transparent;
}


.field .label {
  font-size: 16px;
  color: #515151;
  padding-left: 5px;
  position: absolute;
  transition: 0.3s;
  bottom: 10px;
  pointer-events: none;
}

.field .input:valid ~ .label,
.field .input:focus ~ .label  {
  transition: 0.3s;
  padding-left: 2px;
  transform: translateY(-18px);
  color: crimson;
  filter: brightness(1.25);
}

.input:focus{
  outline: none;
}

.bar:before, .bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 0px;
  position: absolute;
  background: crimson;
  transition: 0.5s ease all;
  filter: brightness(1.25);
}

.bar:before {
  left: 50%;
}

.bar:after {
  right: 50%;
}

.input:focus ~ .bar:before, .input:focus ~ .bar:after {
  width: 50%;
}


button{
  background-color: crimson;
  color: #e7e7e7;
  align-self: center;
  border: none;
  border-radius: 50px;
  width: 40%;
  padding: 10px;
} 

button:hover{
  background-color: rgb(167, 16, 46);
}

.submitbutton{
  margin-top: 25px;
  margin-bottom: 25px;
  width: 80%;
}

@media screen and (max-width: 420px) {
  h3{
      font-size: 16px;
  }
  h1{
      font-size: 24px;
  }
  form {
      min-width: 280px;
      width: 100%;
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 0px;
  }
  .field .label {
      font-size: 14px;
  }
  .input{
      font-size: 14px;
  }
  button{
      font-size: 12px;
  } 
}